body {
	margin: 0;
	min-width: 320px;
	background: #fff;
	color: #666666;
	font: 400 1.4rem/3.1rem "Open Sans", sans-serif;
	font-smoothing: antialiased;
	text-rendering: optimizeLegibility;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	display: flex;
	flex-direction: column;
}

.sr-only {
	position: absolute;
	width: 1px;
	height: 1px;
	padding: 0;
	overflow: hidden;
	clip: rect(0, 0, 0, 0);
	white-space: nowrap;
	border: 0;
}

a {
	text-decoration: none;
	color: inherit;
	transition: all 0.3s ease;
}

a:hover {
	text-decoration: none;
	color: #E8BF5D;
}

button,
input[type="search"],
input[type="button"],
input[type="reset"],
input[type="submit"] {
	-webkit-appearance: none;
	cursor: pointer;
	transition: all 0.3s ease;
}

button::-moz-focus-inner,
input[type="reset"]::-moz-focus-inner,
input[type="button"]::-moz-focus-inner,
input[type="submit"]::-moz-focus-inner {
	border: none;
	padding: 0;
}

.wrapper {
	min-height: 100vh;
	min-height: calc(var(--vh, 1vh) * 100);
	display: flex;
	flex-direction: column;
}

#main {
	flex-grow: 1;
}

h1, h2, h3, h4, h5, h6 {
	font-family: "Playfair Display", serif;
	color: #111111;
	font-weight: 700;
	margin: 0;
	line-height: 1.474;
}

h1 {
	font-size: 6.5rem;
}

h2 {
	font-size: 3.1rem;
}

h3 {
	font-size: 2.1rem;
}

h4 {
	font-size: 2rem;
}

h5 {
	font-size: 1.8rem;
}

h6 {
	font-size: 1.4rem;
}

p {
	margin: 0;
}

input[type="submit"],
input[type="button"],
button,
.button,
.btn {
	display: inline-block;
	background-color: #E8BF5D;
	border: none;
	color: #111111;
	transition: all .3s ease-in;
	padding: 1.1rem 3.7rem;
	border-radius: 2.5rem;
	font-weight: bold;
	letter-spacing: 1px;
	text-transform: capitalize;
}

input[type="submit"]:hover,
input[type="button"]:hover,
button:hover,
.button:hover,
.btn:hover {
	background-color: #e2ad30;
	color: #111111;
}

input[type="text"],
input[type="email"],
input[type="password"],
input[type="number"],
input[type="tel"],
textarea,
select {
	transition: all .3s ease-in;
}

*, *::before, *::after {
	box-sizing: border-box;
}

img {
	max-width: 100%;
	height: auto;
}

.bg-img {
	background-position: 50% 50%;
	background-repeat: no-repeat;
	background-size: cover;
}

.bg-img-abs {
	position: relative;
}

.bg-section-img {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	object-fit: cover;
	font-family: 'object-fit: cover;';
}

.block-head {
	position: relative;
	padding-left: 1.8rem;
	margin-bottom: 5.9rem;
	font-size: 2.1rem;
	font-family: "Playfair Display", serif;
	max-width: 78.7rem;
	line-height: 3.1rem;
	letter-spacing: 0.08rem;
}

.block-head:before {
	content: '';
	position: absolute;
	left: -0.1rem;
	top: 1rem;
	height: calc(100% - 1rem);
	width: 0.1rem;
	background-color: #E8BF5D;
}

.block-head h2 {
	letter-spacing: 0.14rem;
	margin-bottom: 1.2rem;
	font-weight: 400;
	line-height: 4.5rem;
	text-transform: capitalize;
}

.block-head h2.h2 {
	text-transform: none;
}

.block-head p {
	margin-bottom: 2rem;
}

.block-head p + p {
	margin-bottom: 0;
}

.block-head span {
	position: absolute;
	font-family: "Open Sans", sans-serif;
	left: 1rem;
	top: -3.3rem;
	z-index: 1;
	font-size: 184px;
	line-height: 1;
	left: -2px;
	letter-spacing: 15px;
	color: rgba(51, 51, 51, 0.1);
}

.block-head span.color-white-opacity {
	color: rgba(255, 255, 255, 0.2);
}

.color-white {
	color: #fff;
}

.color-light-gray {
	color: #B9B9B9;
}

/* buttons */
.btn {
	white-space: normal;
}

.btn .fa {
	margin-left: 5px;
}

/* header */
#header {
	background: #fff;
	padding: 3.3rem 0 3.2rem;
	position: relative;
}

#header .container {
	display: -webkit-flex;
	display: -ms-flex;
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	color: #111111;
}

#header .container .logo {
	font-family: "Playfair Display", serif;
	font-size: 2.5rem;
	letter-spacing: 1px;
	line-height: 4.5rem;
	text-transform: uppercase;
	font-weight: bold;
	margin: 0;
	padding: 0;
}

#header .container .main-nav {
	flex-grow: 1;
}

#header .container ul {
	display: -webkit-flex;
	display: -ms-flex;
	display: flex;
	justify-content: flex-end;
	margin: 0;
	padding: 0;
}

#header .container li {
	list-style: none;
	margin-left: 4.3rem;
	position: relative;
	text-transform: uppercase;
	line-height: 4.5rem;
	letter-spacing: 0.5px;
}

@media (max-width: 992px) {
	#header .container li {
		margin-left: 2rem;
	}
}

#header .container li:first-child {
	margin-left: 0;
}

#header .container li a {
	padding: 0 0 1.2rem;
	position: relative;
}

#header .container li a:before {
	content: '';
	width: calc(100% + 2.2rem);
	height: 5px;
	background-color: #E8BF5D;
	position: absolute;
	bottom: -0.4rem;
	left: -1.2rem;
	opacity: 0;
	transition: all .3s ease-in;
}

#header .container li a:hover:before {
	opacity: 1;
}

.fader {
	position: fixed;
	z-index: 9;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	background: rgba(0, 0, 0, 0.6);
	visibility: hidden;
	opacity: 0;
	transition: opacity .3s ease-out, visibility .3s ease-out;
}

@media (max-width: 992px) {
	#header .container a:before {
		display: none;
	}
	#header .container .open-menu {
		position: relative;
		width: 3rem;
		height: 3rem;
		border: 0.3rem solid transparent;
		display: block;
		font-size: 0;
		line-height: 0;
	}
	#header .container .open-menu span {
		position: absolute;
		top: 50%;
		left: 50%;
		width: 100%;
		transform: translate(-50%, -50%);
		border-bottom: 0.3rem solid #000;
		height: 0;
	}
	#header .container .open-menu span:nth-child(1) {
		margin-top: -1rem;
	}
	#header .container .open-menu span:nth-child(3) {
		margin-top: 1rem;
	}
	.menu-opened #header .container .open-menu span:nth-child(1) {
		margin-top: 0;
		transform: translate(-50%, -50%) rotate(45deg);
	}
	.menu-opened #header .container .open-menu span:nth-child(2) {
		display: none;
	}
	.menu-opened #header .container .open-menu span:nth-child(3) {
		margin-top: 0;
		transform: translate(-50%, -50%) rotate(-45deg);
	}
	.menu-opened .fader {
		visibility: visible;
		opacity: 1;
		z-index: 10;
	}
	.wrapper {
		position: relative;
		left: 0;
		transition: left .3s ease-out;
	}
	.menu-opened .wrapper {
		left: -23rem;
	}
	#header .container {
		z-index: 2;
	}
	#header .container ul {
		position: fixed;
		right: 0;
		top: 0;
		bottom: 0;
		width: 23rem;
		display: block;
		background: #E8BF5D;
		z-index: 15;
		transform: translateX(100%);
		transition: transform .3s ease-out;
	}
	.menu-opened #header .container ul {
		transform: translateX(0);
	}
	#header .container ul li {
		text-align: left;
		margin-left: 0;
		border-bottom: 1px solid #fff;
	}
	#header .container ul a {
		display: block;
		text-align: center;
		padding: 1.5rem;
		font-size: 1.6rem;
		line-height: 1.2;
		color: #fff;
	}
	#header .container ul a:hover {
		background: #B9B9B9;
		color: #E8BF5D;
	}
}

/* footer */
#footer {
	padding: 2.3rem 0 2.7rem;
	background: #111111;
}

#footer .container {
	display: -webkit-flex;
	display: -ms-flex;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

@media (max-width: 576px) {
	#footer .container {
		flex-direction: column;
		align-items: center;
		margin-right: 0;
	}
}

#footer .copy {
	color: #B5B5B5;
	line-height: 1.6rem;
	font-family: "Playfair Display", serif;
	font-size: 1.6rem;
	letter-spacing: -0.03rem;
}

#footer .copy a {
	color: #fff;
	font-weight: 400;
}

#footer .copy a:hover {
	color: #E8BF5D;
}

#footer .socials {
	list-style: none;
	display: -webkit-flex;
	display: -ms-flex;
	display: flex;
	margin: 0;
	margin-right: 7rem;
	padding: 0;
	color: #fff;
	font-size: 1.6rem;
}

@media (max-width: 576px) {
	#footer .socials {
		margin-right: 0;
	}
}

#footer .socials li {
	margin-left: 1.7rem;
	line-height: 3.5rem;
}

#footer .socials li:first-child {
	margin-left: 0;
}

