html {
	font-size: 10px;
}

body {
	margin: 0;
	min-width: 320px;
	background: $body-bg;
	color: $text-color-base;
	font: $font-weight-base #{$font-size-base}/$line-height-base $font-family-base;
	font-smoothing: antialiased;
	text-rendering: optimizeLegibility;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	display: flex;
	flex-direction: column;
}

.sr-only {
	position: absolute;
	width: 1px;
	height: 1px;
	padding: 0;
	overflow: hidden;
	clip: rect(0, 0, 0, 0);
	white-space: nowrap;
	border: 0;
}

a {
	text-decoration: none;
	color: $links-color-base;
	transition: $transition;
}

a:hover {
	text-decoration: none;
	color: $link-hover-color;
}

button,
input[type="search"],
input[type="button"],
input[type="reset"],
input[type="submit"] {
	-webkit-appearance: none;
	cursor: pointer;
	transition: $transition;
}

button::-moz-focus-inner,
input[type="reset"]::-moz-focus-inner,
input[type="button"]::-moz-focus-inner,
input[type="submit"]::-moz-focus-inner {
	border: none;
	padding: 0;
}

.wrapper {
	min-height: 100vh;
	min-height: calc(var(--vh, 1vh) * 100);
	display: flex;
	flex-direction: column;
}

#main {
	flex-grow: 1;
}

h1, h2, h3, h4, h5, h6{
	font-family: $font-title;
	color: $dark;
	font-weight: 700;
	margin: 0;
	line-height: $line-height-title;
}
h1 {
	font-size: $fs-h1-title;
}

h2 {
	font-size: $fs-h2-title;
}

h3 {
	font-size: $fs-h3-title;
}
h4 {
	font-size: $fs-h4-title;
}
h5 {
	font-size: $fs-h5-title;
}
h6 {
	font-size: $fs-h6-title;
}

p {
	margin: 0;
}

input[type="submit"],
input[type="button"],
button,
.button,
.btn {
	display: inline-block;
	background-color: $btn-bg;
	border: none;
	color: $btn-color;
	transition: all .3s ease-in;
	padding: 1.1rem 3.7rem;
	border-radius: 2.5rem;
	font-weight: bold;
	letter-spacing: 1px;
	text-transform: capitalize;
}

input[type="submit"]:hover,
input[type="button"]:hover,
button:hover,
.button:hover,
.btn:hover {
	background-color: darken($btn-bg, 10%);
	color: $btn-color;
}

input[type="text"],
input[type="email"],
input[type="password"],
input[type="number"],
input[type="tel"],
textarea,
select {
	transition: all .3s ease-in;
}

*, *::before, *::after {
	box-sizing: border-box;
}

img {
	max-width: 100%;
	height: auto;
}
